
export const titleValid=(title)=>{
    let valid = /([a-zA-Z()"'&.,!\s])+/;
    if(title.length>=3){
        if(valid.test(title)){
            return {valid:true}
        }else{
            return {valid:false,msg:'Validation error!'}
        }
    }else{
        return {valid:false,msg:'Title is too short!'}
    }
}


export const descValid=(desc)=>{
    let valid = /([A-Za-z0-9.,!'"&\s])+/
    if(desc.length>=5){
        if(valid.test(desc)){
            return {valid:true}
        }else{
            return {valid:false,msg:'Validation error!'}    
        }
    }else{
        return {valid:false,msg:'Description is too short!'}
    }
}

export const urlValid=(url)=>{
    let valid = /^(https?|chrome):\/\/[^\s$.?#].[^\s]*$/
    if(valid.test(url)){
        return {valid:true};
    }
    return {valid:false,msg:'url is not valid!'};
}